import React,{useState,useEffect} from 'react';
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CTooltip,
  CRow,
  CButton,
  CPagination
} from '@coreui/react';



import {
  useDispatch,
   useSelector
  } from 'react-redux'
    
  import {
    useFormFields,
    disableSubmitButton
 }  from '../../../helpers/hooksFormInput'
 import {
     cleanUpData,
     instantiateAbort
    } from '../../../helpers/componentHelperFunc'

    
    import Loader from 'react-loader-spinner'

import { NavLink } from 'react-router-dom';

import {viewEventDetailsAction} from '../../../store/Actions/UserEventAction'




export default function EventDetailsLists(props)  
{
   
 

  const dispatch =  useDispatch();
     
  const EventLists = useSelector(state => state.eventReducer.eventListResponse);

   
  const abortEffect = instantiateAbort();

  const perPage = 5;
  
  const [currentPage, setCurrentPage] = useState(1)

   useEffect(() => {
      
        dispatch( viewEventDetailsAction(perPage,1,props.filterData.input,props.filterData.option)  );
    
     return () => {
       cleanUpData(abortEffect)
     };
   }, [props.filterData])

    useEffect(() => {
      
     dispatch(viewEventDetailsAction(perPage,1,null,null));
      return () => {
        cleanUpData(abortEffect);
      }
    }, []);


  

  const loadPageItem = (nextOrPrevPageClicked) =>
  {
    if(props.hasOwnProperty("filterData") && props.filterData!=null
     && props.filterData!="")
    {
      dispatch(viewEventDetailsAction(perPage,nextOrPrevPageClicked,
        props.filterData.input,props.filterData.option));
  
    setCurrentPage(nextOrPrevPageClicked);
    }else
    {
      dispatch(viewEventDetailsAction(perPage,nextOrPrevPageClicked,
        props.filterData.input,props.filterData.option));
  
    setCurrentPage(nextOrPrevPageClicked);
    }
    
  } 




  return (
    <>
    <CRow>
        <CCol>
          <CCard>
            <CCardHeader>
             Event Lists
            </CCardHeader>
            <CCardBody>
               
               {
                  EventLists == 'loading' || EventLists == '' ?

                  <Loader type="ThreeDots" color="#321fdb" height={100} width={100} />
                  :
                  EventLists.hasOwnProperty('data') && EventLists.success == true ?
                    <>
<table className="table table-bordered table-striped table-responsive">
    <thead>
      <tr>
        <th style={{minWidth:200}}>Event Header</th>
        <th style={{minWidth:200}}>Username</th>
        <th style={{minWidth:200}}>Email</th>
        <th style={{minWidth:200}}>Cost</th>
        <th style={{minWidth:200}}>Status</th>
        <th style={{minWidth:200}}>Date Scheduled</th>
        <th style={{minWidth:200}}>Views Count</th>
        <th style={{minWidth:200}}>Paid Fans</th>
        <th style={{minWidth:200}}>User Profile</th>
        <th style={{minWidth:200}}>Date Created</th>
      </tr>
    </thead>
    <tbody>
      {  
        EventLists.data.data.map((data)=>(
  
          <tr key={data.id}>
   
          <td>{data.event_header}</td>
          <td>{data.username}</td>
          <td>{data.email}</td>
      <td>{"₦"+parseInt(data.cost).toLocaleString()}</td>
      <td>{data.event_live_now==0?
      <span>Future Event</span>
      :data.event_live_now==1?
      <span>Currently Live</span>
      :data.event_live_now==2?
      <span>Past Event</span>
      :
      null
      }</td>
       <td>
         {
          new Date(parseInt(data.event_time_scheduled)).toString()
         }
       </td>
          <td>
            {data.views_count}
          </td>
          <td>
            {
               <NavLink to={`/paid-fans/${data.id}`} >
               <CBadge color="primary" style={{cursor:"pointer"}}>
                <i className="fa fa-money" aria-hidden="true"></i>
               </CBadge>
               </NavLink>
            }
          </td>
          <td>
            <NavLink to={`/view-user/${data.user_id}`} >
            <CBadge color="primary" style={{cursor:"pointer"}}>
             <i className="fa fa-eye" aria-hidden="true"></i>
            </CBadge>
            </NavLink>
           
            
            </td>
        <td>{data.created_at}</td>
    </tr>

        ))
      }
     
      
    </tbody>
  </table>

<div style={{marginLeft:10}}>
            <CPagination
            size="sm"
            activePage={currentPage}
            pages={EventLists.data.total}
            onActivePageChange={loadPageItem}
            pages={EventLists.data.last_page}
          />
                 
            
            </div>
            </>
            :

            ""
            
           }


            </CCardBody>
            
           

          </CCard>
        </CCol>
      </CRow>
    </>
  );
}
  