import { RESTART_EVENT_DETAILS_RESPONSE, EVENT_DETAILS_LOADING, EVENT_DETAILS_SUCCESS, 
    EVENT_DETAILS_ERROR, EVENT_DETAILS_CODE_ERROR, RESTART_USERS_PAID_FOR_EVENT_DETAILS_RESPONSE, 
    USERS_PAID_FOR_EVENT_DETAILS_LOADING, USERS_PAID_FOR_EVENT__DETAILS_SUCCESS,
     USERS_PAID_FOR_EVENT_DETAILS_ERROR, USERS_PAID_FOR_EVENT_DETAILS_CODE_ERROR, RESTART_REVENUE_PER_EVENT_RESPONSE, REVENUE_PER_EVENT_LOADING, REVENUE_PER_EVENT_SUCCESS, REVENUE_PER_EVENT_ERROR, REVENUE_PER_EVENT_CODE_ERROR, RESTART_REVENUE_PER_CELEBRITY_RESPONSE, REVENUE_PER_CELEBRITY_LOADING, REVENUE_PER_CELEBRITY_SUCCESS, REVENUE_PER_CELEBRITY_ERROR, REVENUE_PER_CELEBRITY_CODE_ERROR } from "../ActionTypes/UserEventActionTypes";
import { EventListService, PaidUsersPerEventService,
     TotalRenuePerEventService,
    TotalRevenuePerCelebrityService } from "../../services/UsersEventDetailService";

export const viewEventDetailsAction = (perPage,pageIndex,input,options) =>
{
return (dispatch)=>{

 dispatch({type:RESTART_EVENT_DETAILS_RESPONSE});
 dispatch({type:EVENT_DETAILS_LOADING});

  EventListService(perPage,pageIndex,input,options).then((res)=>{

         console.log(res);

         if(res.hasOwnProperty('success') && res.success==true){
          dispatch({type:EVENT_DETAILS_SUCCESS ,res});
        

         }else if(res.hasOwnProperty('success') && res.success==false) {
              dispatch({type:EVENT_DETAILS_ERROR,res});  
         }
     },
     error=>{
      dispatch({type:EVENT_DETAILS_CODE_ERROR,error});
         console.log(error);
     }
 )
} 

}

export const clearEventListState= ()=>
{
return (dispatch)=>
{
 dispatch({type:RESTART_EVENT_DETAILS_RESPONSE});
}
}

