import HttpService from './HttpService';
import Cookies from 'js-cookie'




export const EventListService = (perPage,pageIndex,input,options) =>
{
    const http = new HttpService();
    
    let addedUrl = "";
    /**
     * if input and options select is empty
     */
    if((input==null || input=="") && (options==null || options=="")) 
    {
        addedUrl = "admin/get-event-list/"+perPage+"?page="+pageIndex;
    }
    else if((input!=null || input !="") && (options==null || options=="") )
    {
        addedUrl = "admin/filter-event-by-input/"+input+"/"+perPage+"?page="+pageIndex;
    }
    else if((input==null || input =="") && (options!=null || options!=""))
    {
        addedUrl = "admin/filter-event-by-options/"+options+"/"+perPage+"?page="+pageIndex;
    }
    else if((input!=null || input !="") && (options!=null || options!=""))
    {
        addedUrl = "admin/filter-event-by-input-and-options/"+options+"/"+input+"/"+perPage+"?page="+pageIndex;
    }
    
    let tokenId = 'vt-music';
    return http.getData(addedUrl,tokenId).then((data)=>
    {
        console.log(data)
        return data;

    }).catch((error)=>
    {
        console.log(error)
        return error;
    });
}

